@keyframes intro {
  0% {
    transform: translateY(100%);
    opacity: 0;
  }

  10% {
    opacity: 1;
  }
}

.wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  pointer-events: none;
  z-index: 3;
}

.item {
  width: 100%;
  display: flex;
  animation: intro .3s;
  pointer-events: auto;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.item.viewDanger {
  background-color: #E55565;
}

.item.viewWarning {
  background-color: #F69D00;
}

.content {
  flex-grow: 1;
  padding: 1em;
}

.dismiss {
  padding: 1em;
  font-size: inherit;
  border: none;
  background: none;
  color: rgba(0, 0, 0, 0.4);
}




