.wrapper {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 2;
  background-color: #FFFFFF;
  border-radius: 1em;
  padding: 1em;
  transform: translateY(100%);
  visibility: hidden;
  transition-property: transform, visibility;
  transition-duration: .3s;
  display: flex;
  flex-direction: column;
}

.wrapper.isActive {
  transform: none;
  visibility: visible;
}

.head {
  flex-shrink: 0;
}

.content {
  flex-grow: 1;
  display: flex;
}




