.wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
	min-height: 100%;
	text-align: center;

	> * {
		flex-grow: 1; }

	input {
		border: none;
		border-bottom: 1px solid;
		padding: .5em 0;
		font-size: 1.2em;
		outline: none; }

	button {
		border: none;
		outline: none;
		background: none;
		font-size: inherit;
		padding: 1em;

		i {
			margin-left: .5em; } } }



