.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  font-size: 2em;
}

.wrapper button {
  padding: 1em;
  border: none;
  background: none;
  font-size: inherit;
  opacity: .5;
}

.wrapper button:hover,
.wrapper button:focus {
  opacity: 1;
}




